<template>
  <div class="layout">
    <Row>
      <Col>
        <NavBar :activeName="'home'"></NavBar>
      </Col>
    </Row>
    <Row>
      <Col class="w1200">
        <Layout>
          <Row>
            <Content class="content">
              <Layout>
                <Content class="content-center">
                  
                </Content>
              </Layout>
            </Content>
          </Row>
        </Layout>
      </Col>
    </Row>
    <Row>
      <Col>
        <Footer></Footer>
      </Col>
    </Row>
  </div>
</template>

<script>
import NavBar from '../components/NavBar.vue' // 
import Footer from '../components/Footer.vue' // 
export default {
  name: 'Home',
  components: {
    NavBar, // 
    Footer // 
  },
  data () {
    return {
      msg: 'Home'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .layout {
    min-width: 800px;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .w1200 {
    margin: 0 auto;
    width: 1200px;
  }
  .content {
    margin: 50px auto;
    min-height: 530px;
    background: #fff;
  }
  .content-center {
    padding: 24px;
    background: #fff;
  }
</style>



